import React from 'react';
import './css/NavBar.css';
import Logo from './Logo.js';
import { NavLink, Link } from 'react-router-dom';


function NavBar() {

    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="container-fluid">
                <Link exact to='/home' className='Logo-main'><Logo /></Link>

                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup"
                    aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <NavLink className='nav-link' exact to='/home'>Home</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className='nav-link ' exact to='/menu'>Menu</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className='nav-link' exact to='/location'>Location</NavLink>
                        </li>
                    </ul>
                    <ul className='navbar-nav ms-auto'>
                        <li className="nav-item justify-content-end">
                            <NavLink className='nav-link' exact to='/SocialMedia'>
                                <img src="https://img.icons8.com/office/30/000000/facebook-new.png" />
                                <img src="https://img.icons8.com/fluent/30/000000/instagram-new.png" />
                                <img src="https://img.icons8.com/color/30/000000/twitter--v1.png" />
                            </NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}

export default NavBar;