import React from 'react';
import './css/SocialMediaPage.css'

// let Facebook="https://img.icons8.com/ios-filled/400/000000/facebook-new.png"
//     let Instagram="https://img.icons8.com/ios/50/000000/instagram-new--v1.png"
//     let Twitter="https://img.icons8.com/ios/50/000000/twitter--v1.png"

function SocialMediaPage(props) {
    let site=props.match.params.site;
    let len=site.length;
    let siteFixed=site.substring(1, len);

    let siteImg;

    if (siteFixed=='Instagram') { siteImg='https://img.icons8.com/ios/50/000000/instagram-new--v1.png' }
    if (siteFixed=='Facebook') { siteImg='https://img.icons8.com/ios-filled/400/000000/facebook-new.png' }
    else if (siteFixed=='Twitter') { siteImg='https://img.icons8.com/ios/50/000000/twitter--v1.png' }



    console.log(siteImg)

    return (
        <div className='SM-link'>

            <h1>This is a placeholder page for {siteFixed} <img src={siteImg} />


            </h1>

            <div className='SM-textArea'>
                <p>The Link that sent you here would normally send you <br />
                    To WonTonSoup's {siteFixed} page. If this business actually existed ofc</p>
            </div>
        </div>
    )


}

export default SocialMediaPage;