import React from 'react';
import './css/SocialMedia.css';
import { Link } from 'react-router-dom';

function SocialMedia() {

    return (
        <div className='SocialMedia'>
            <h2>This is where I would put the social media links of the restaurant if there were any</h2>
            <br />

            <div className='SocialMedia-links'>
                <Link className='SocialMedia-links Facebook' to='/SocialMedia/:Facebook'>Facebook <img src="https://img.icons8.com/color/48/000000/facebook.png" /></Link>
                <br />
                <Link className='SocialMedia-links Instagram' to='/SocialMedia/:Instagram'>Instagram <img src="https://img.icons8.com/fluent/48/000000/instagram-new.png" /></Link>
                <br />
                <Link className='SocialMedia-links Twitter' to='/SocialMedia/:Twitter'>Twitter <img src="https://img.icons8.com/color/48/000000/twitter-squared.png" /></Link>

            </div>

        </div>
    )
}

export default SocialMedia;